import fs from "fs"
import path from "path"
import Tool from "./Tool.mjs"

const tool = new Tool()
// package.json
class Pkg {
  constructor() {
    const dirPath = process.cwd() // 
    this.path = path.join(dirPath, "package.json")
    this.dirPath = dirPath
    // 
    this._cache = null
  }
  // package.json
  get() {
    const defaultInfo = {
      scripts: {}
    }
    // package.json
    let info
    if (fs.existsSync(this.path)) {
      const infoStr = fs.readFileSync(this.path, "utf-8")
      try {
        info = JSON.parse(infoStr)
        // console.log(info, 'tryinfo')
      }catch (e){
        info = defaultInfo
      }
      if (!tool.isObject(info) || !tool.isObject(info.scripts)) {
        info = defaultInfo
      }
    }else{
      tool.writeJSONFileSync(this.path, defaultInfo)
      info = defaultInfo
    }
    // info
    return info
  }
  update(key, content) {
    // 
    const filepath = this.path
    const info = this.get() // key'scripts'
    const SCRIPTS = "scripts"

    if (key === SCRIPTS) {
      info[SCRIPTS] = { ...info[SCRIPTS], ...content }
    } else {
      info[key] = content
    }
    tool.writeJSONFileSync(filepath, info)
  }
}

export default Pkg
